/* 🎴 Solitaire.css — современный адаптивный стиль для игры "Косынка" */

/* === Цветовые переменные === */
:root {
  --primary-pink: #ff6b8b;
  --secondary-pink: #ff8fa3;
  --light-pink: #ffb3c6;
  --gold: #ffd166;
  --purple: #c77dff;
  --blue: #48cae4;
  --text-dark: #2d3047;
  --text-light: #ffffff;
  --shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
  --success-green: #4CAF50;
  --warning-orange: #FF9800;
}

/* === Общие стили === */
body {
  background: linear-gradient(180deg, #c77dff 0%, #ff8fa3 100%);
  background-attachment: fixed;
  margin: 0;
  padding: 0;
  font-family: 'Montserrat', sans-serif;
  color: var(--text-light);
  -webkit-tap-highlight-color: transparent;
  user-select: none;
}

.solitaire-page {
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  padding: 1rem;
  overflow: auto;
}

.solitaire-container {
  text-align: center;
  max-width: 900px;
  width: 95%;
  padding: 1.5rem;
  background: rgba(255, 255, 255, 0.15);
  backdrop-filter: blur(20px);
  border-radius: 25px;
  border: 1px solid rgba(255, 255, 255, 0.2);
  box-shadow: var(--shadow);
  margin: 0.5rem auto;
  position: relative;
  z-index: 10;
}

/* === Заголовок === */
.game-title {
  font-family: 'Dancing Script', cursive;
  font-size: 2.5rem;
  color: white;
  margin-bottom: 1rem;
  text-shadow: 3px 3px 10px rgba(0, 0, 0, 0.3);
  letter-spacing: 1px;
}

/* === Статистика === */
.game-stats {
  display: flex;
  justify-content: space-around;
  margin: 1rem auto;
  color: white;
  max-width: 400px;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 15px;
  padding: 0.5rem;
  flex-wrap: wrap;
  gap: 0.5rem;
}

.stat-item {
  text-align: center;
  flex: 1;
}

.stat-value {
  font-size: 1.8rem;
  font-weight: 700;
  color: var(--gold);
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3);
  transition: transform 0.3s ease;
}

.stat-value:hover {
  transform: scale(1.1);
}

/* === Основное поле === */
.solitaire-game {
  margin: 1rem 0;
}

/* Верхний ряд */
.top-row {
  display: flex;
  justify-content: space-between;
  margin-bottom: 2rem;
  gap: 1rem;
  flex-wrap: wrap;
  align-items: center;
}

.deck-container {
  display: flex;
  gap: 1rem;
  justify-content: center;
}

.deck-placeholder {
  width: 80px;
  height: 112px;
  border-radius: 8px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: all 0.3s ease;
  position: relative;
  border: 2px dashed rgba(255, 255, 255, 0.3);
}

.deck-placeholder:hover {
  transform: translateY(-5px);
  box-shadow: 0 5px 15px rgba(255, 255, 255, 0.2);
}

.foundations {
  display: flex;
  gap: 0.5rem;
  justify-content: center;
}

.foundation {
  width: 80px;
  height: 112px;
  border: 2px dashed rgba(255, 255, 255, 0.3);
  border-radius: 8px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: all 0.3s ease;
}

.foundation:hover {
  background: rgba(255, 255, 255, 0.1);
  transform: translateY(-2px);
}

/* === Игровое поле (tableau) === */
.tableau {
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  gap: 0.8rem;
  margin: 1rem 0;
  min-height: 200px;
}

.tableau-column {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.5rem;
  min-height: 150px;
  cursor: pointer;
  transition: all 0.3s ease;
  border-radius: 8px;
  padding: 5px;
  position: relative;
}

.tableau-column:hover {
  background: rgba(255, 255, 255, 0.05);
}

/* === Карты === */
.card {
  width: 80px;
  height: 112px;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 0.5rem;
  font-weight: 600;
  cursor: pointer;
  transition: transform 0.25s ease, box-shadow 0.25s ease;
  user-select: none;
  position: relative;
  background: white;
  box-shadow: 0 3px 10px rgba(0, 0, 0, 0.15);
}

.card:active {
  transform: scale(0.97);
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.2);
}

.card-back {
  width: 100%;
  height: 100%;
  background: linear-gradient(135deg, var(--primary-pink), var(--purple));
  border-radius: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.5rem;
  color: white;
}

.card.face-up.red {
  color: #e74c3c;
}

.card.face-up.black {
  color: #2c3e50;
}

.card-rank {
  font-size: 1.2rem;
  font-weight: 700;
  position: absolute;
  top: 5px;
  left: 5px;
}

.card-suit {
  font-size: 1.5rem;
  text-align: center;
  margin-top: 25px;
}

.card.selected {
  transform: translateY(-10px);
  box-shadow: 0 5px 20px rgba(255, 217, 0, 0.897);
}

/* === Кнопки === */
.game-controls {
  margin: 1.5rem 0;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 1.2rem;
  flex-wrap: wrap;
}

.game-button,
.back-button {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
  background: linear-gradient(135deg, #758cf5 0%, #8f5ac4 100%);
  border: none;
  padding: 14px 24px;
  font-size: 1rem;
  color: #ffffff;
  border-radius: 20px;
  cursor: pointer;
  transition: all 0.25s ease;
  font-weight: 700;
  position: relative;
  overflow: hidden;
  text-decoration: none;
  letter-spacing: 0.5px;
  box-shadow: 0 4px 0 #6e4597, 0 8px 16px rgba(0, 0, 0, 0.15);
  touch-action: manipulation;
}

.game-button:hover,
.back-button:hover {
  transform: translateY(-3px);
  background: linear-gradient(135deg, #a3f0ff 0%, #85b8ff 100%);
  box-shadow: 0 5px 0 #1b3e69, 0 10px 20px rgba(0, 0, 0, 0.25);
}

.game-button:active,
.back-button:active {
  transform: translateY(2px);
  box-shadow: 0 2px 0 #2e6bb3, 0 4px 8px rgba(0, 0, 0, 0.2);
}

/* === Сообщение-комплимент === */
.compliment-message {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) scale(0);
  background: linear-gradient(135deg, var(--primary-pink), var(--purple));
  color: white;
  padding: 1.5rem;
  border-radius: 15px;
  text-align: center;
  z-index: 1000;
  box-shadow: 0 20px 50px rgba(0, 0, 0, 0.3);
  transition: all 0.5s ease;
  max-width: 300px;
  width: 90%;
}

.compliment-message.show {
  transform: translate(-50%, -50%) scale(1);
  animation: bounceIn 0.6s ease;
}

@keyframes bounceIn {
  0% { transform: translate(-50%, -50%) scale(0.3); opacity: 0; }
  50% { transform: translate(-50%, -50%) scale(1.1); }
  100% { transform: translate(-50%, -50%) scale(1); opacity: 1; }
}

.message-heart {
  font-size: 2rem;
  margin-bottom: 0.5rem;
  animation: heartBeat 1.5s ease-in-out infinite;
}

@keyframes heartBeat {
  0%, 100% { transform: scale(1); }
  50% { transform: scale(1.3); }
}

/* === Мобильная адаптация === */
@media (max-width: 768px) {
  html { font-size: 15px; }

  .solitaire-container {
    padding: 1rem;
    border-radius: 20px;
  }

  .game-title {
    font-size: 1.8rem;
  }

  .top-row {
    flex-direction: column;
    gap: 0.6rem;
  }

  .tableau {
    display: flex;
    overflow-x: auto;
    scroll-snap-type: x mandatory;
    gap: 0.6rem;
    padding: 0.5rem;
  }

  .tableau-column {
    flex: 0 0 auto;
    scroll-snap-align: start;
  }

  .card {
    width: 55px;
    height: 78px;
  }

  .deck-placeholder,
  .foundation {
    width: 60px;
    height: 84px;
  }

  .game-controls {
    gap: 0.8rem;
  }
}

@media (max-width: 480px) {
  html { font-size: 14px; }

  .card {
    width: 45px;
    height: 63px;
  }

  .deck-placeholder,
  .foundation {
    width: 48px;
    height: 68px;
  }

  .game-title {
    font-size: 1.5rem;
  }

  .stat-value {
    font-size: 1.3rem;
  }

  .compliment-message {
    font-size: 0.9rem;
  }
}

/* === 🌙 Тёмная тема === */
@media (prefers-color-scheme: dark) {
  body {
    background: linear-gradient(180deg, #2d3047 0%, #4b4e6d 100%);
  }

  .solitaire-container {
    background: rgba(255, 255, 255, 0.08);
    border-color: rgba(255, 255, 255, 0.15);
  }

  .stat-value {
    color: var(--blue);
  }

  .card.face-up {
    background: #fdfdfd;
  }
}
